.search-wrap {
    padding: 25px 0 52px 0;

}

.search-wrap form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
}

.search {
    color: #939393;
    font-size: 16px;
    border-radius: 20px;
    padding: 10px 30px;
    width: 270px;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(20px);
    border: none;
}


@media (min-width: 768px) {
    .search-wrap form {
        flex-direction: row;
        justify-content: center;
        padding: 25px 0 123px 0;
    }
}


@media (min-width: 1200px) {
    .search-wrap form {
        padding: 25px 0 133px 0;
    }
}

.countries option {
    background-color: rgba(255, 255, 255, 0.2);
    color: rgb(216, 194, 194);
    font-size: 16px;
    padding: 10px 30px;
    border-radius: 20px;
    cursor: pointer;
}
